import wPicker from "@/components/w-picker/w-picker.vue"; 
export default {
	components:{
		wPicker
	},
	data() {
		return {
			title: 'Hello',
			address:{
				receiver:"",
				phone:"",
				province:"",
				city:"",
				area:"",
				detail:"",
				defaultFlag:0
				
				
			},
			type:'',
			word:""
		}
	},
	onLoad(option) {
		
		// if(option.type){
		// 	this.type = "edit";
			
		// }
	},
	methods: {
		onConfirm(data){
			console.log(data);
			this.address.province = data.checkArr[0];
			this.address.city = data.checkArr[1];
			this.address.area = data.checkArr[2];
			this.word = this.address.province+this.address.city+this.address.area
			
		},
		openPicker(){
			this.$refs.picker.show()
		},
		aaddress(){
			let _this = this;
			console.log(this.address)
			if(_this.address.receiver == ""){
				uni.showToast({
					title:"联系人不能为空",
					"icon":"none"
				})
				return false;
			}
			if(_this.address.phone == ""){
				uni.showToast({
					title:"手机号不能为空",
					"icon":"none"
				})
				return false;
			}
			
			if(!(/^1[3456789]\d{9}$/.test(_this.address.phone))){
				uni.showToast({
					"title":"电话号码格式不正确",
					"icon":"none"
				}) 
				return false; 
			}
			if(_this.address.province == ""){
				uni.showToast({
					title:"请选择地区信息",
					"icon":"none"
				})
				return false;
			}
			if(_this.address.detail == ""){
				uni.showToast({
					title:"请选择详细地址",
					"icon":"none"
				})
				return false;
			}
			
			this.$ajax("/useraddress/add","POST",this.address).then((res)=>{
				uni.showToast({
					"title":"添加成功,正在跳转",
					"icon":"none",
					"mask":"true"
				})
				setTimeout(function(){
					uni.navigateTo({
						"url":"/pages/mine/address"
					})
				},777)
				
			})
		}
	}
}
